import { ref, watch, computed } from 'vue'
import { defineStore } from 'pinia'

import api from '@/api/index'

import getFormConfigStore from './formConfig'

export const useConfigurableOptionConfigStore = getFormConfigStore('configurable_option')

export const useConfigurableOptionStore = defineStore('configurable_option', 
    () => {
        const loading = ref(true)
        const currentOptionId = ref(null) // Option Id we're working on
        const currentOption = ref({}) // The current Configurable Option object
        const optionCollection = ref([]) // The current Configurable Option collection object
        const optionCollectionMeta = ref({}) // Meta data about the collection

        async function setInitialValues(collectionUrl){
            api.configurable_option.setCollectionUrl(collectionUrl)
            loadOptionCollection()
        }
        async function loadOptionCollection(loadOptions={}){
            const result = await api.configurable_option.loadCollection(loadOptions)
            optionCollection.value = result.items
            optionCollectionMeta.value = result.metadata
            loading.value = false
        }
       async function setOptionId(optionId){
            currentOptionId.value = optionId
       }
       async function loadOption(optionId=null){
            if (! optionId) optionId = currentOptionId.value
            const option = await api.configurable_option.load(optionId)
            currentOption.value = option
            loading.value = false
        }
        async function createOption(values){
             currentOption.value = await api.configurable_option.create(values)
             await loadOptionCollection()
             return currentOption.value
        }
       async function updateOption(values, optionId=null){
            
            currentOption.value = await api.configurable_option.update(values, optionId)
            await loadOptionCollection()
            return currentOption.value
       }
       async function save(values, optionId=null){
           if (! values.id && ! optionId){
                return createOption(values);
            } else {
                return updateOption(values, optionId);
            }

       }
       async function deleteOption(optionId){
            await api.configurable_option.delete(optionId)
            await loadOptionCollection()
       }
       return {
            loading, 
            currentOptionId,
            currentOption,
            optionCollection,
            optionCollectionMeta,
            setInitialValues,
            setOptionId,
            loadOption,
            deleteOption,
            save,
       }
    }
)